import React, { useEffect } from 'react'
import { CButton, CCard, CCardBody, CCardHeader, CCol, CRow } from '@coreui/react'
import CIcon from '@coreui/icons-react'

import { useDispatch, useSelector } from 'react-redux'
import { deleteDriver, getDriver, verifyDriver } from 'src/redux/actions'
import { useHistory } from 'react-router'

const Driver = ({ match }) => {
  const dispatch = useDispatch()
  const history = useHistory()
  useEffect(() => {
    dispatch(getDriver(match.params.id))
  }, [dispatch, match])

  const driver = useSelector(state => state.driver)

  const driverDetails = driver ? Object.entries(driver) : 
    [['id', (<span><CIcon className="text-muted" /> Not found</span>)]]
  
  const handleApproveDriver = (id) => {
    dispatch(verifyDriver(id))
  }

  const handleDeleteDriver = (id) => {
    dispatch(deleteDriver(id))
    history.push('/drivers')
  }

  return (
    <CRow>
      <CCol>
        <CCard>
          <CCardHeader>
            Driver id: {match.params.id}
          </CCardHeader>
          <CCardBody>
              <table className="table table-striped table-hover">
                <tbody>
                  {
                    driverDetails.map(([key, value], index) => {
                      return (
                        <tr key={index.toString()}>
                          <td>{`${key}:`}</td>
                          <td><strong>{value}</strong></td>
                        </tr>
                      )
                    })
                  }
                </tbody>
            </table>
            <CRow alignHorizontal="around">
              <CButton disabled={driverDetails.verified} color="success" onClick={() => handleApproveDriver(match.params.id)}>Verify</CButton>
              <CButton color="danger" onClick={() => handleDeleteDriver(match.params.id)}>Delete</CButton>
            </CRow>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  )
}

export default Driver
